/**
 *    .
 */
export class Vector {
  constructor(x, y) {
    this.x = x;
    this.y = y;
  }
  
  get length() {
    return Math.sqrt(Math.pow(this.x, 2) + Math.pow(this.y, 2));
  }

  /**
   *         OX.
   */
  get cosAngle() {
    return this.x / this.length;
  }

  /**
   *         OX.
   */
  get sinAngle() {
    return this.y / this.length;
  }
}